import { Button, CircularProgress, Stack, TextField } from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import { useSnackbar } from "notistack";
import React, { useState } from "react";
import { useHistory, Link } from "react-router-dom";
import { config } from "../App";
import Footer from "./Footer";
import Header from "./Header";
import "./Login.css";
function Login() {
  const { enqueueSnackbar } = useSnackbar();
  const history = useHistory();
  const [data,setData] = useState({});
  const [loading,setLoading] =useState(false);
  const handleInput = async (e) =>{
      setData(() => ({
        ...data,
        [e.target.name] : e.target.value
      }))
    }

  // TODO: CRIO_TASK_MODULE_LOGIN - Fetch the API response
  /**
   *
   * Example for successful response from backend:
   * HTTP 201
   * {
   *      "success": true,
   *      "token": "testtoken",
   *      "username": "criodo",
   *      "balance": 5000
   * }
   *
   * Example for failed response from backend:
   * HTTP 400
   * {
   *      "success": false,
   *      "message": "Password is incorrect"
   * }
   *
   */
  const performApiCall = async () => {
    let responsedata;
    let error= false;
    setLoading(true);
    try{
    let response = await axios.post(`${config.endpoint}/auth/login`,{
      username : data.username,
      password : data.password
    });
    responsedata= response.data;
    setLoading(false)
  }catch(e){
    if(e.response.status==400) {
      responsedata = e.response.data;
    }else error = true;
    setLoading(false)
  }
  if(await validateResponse(responsedata,error)){
    await persistLogin(responsedata.token,responsedata.username,responsedata.balance)
    return responsedata;
  }
  }
  
  const validateResponse = async (response,error) => {
    if(error || (!response.success && !response.message)){
      enqueueSnackbar("Something went wrong. Check that the backend is running, reachable and returns valid JSON.",{ variant: `error` });
      return false;
    }
    if(!response.success ){
      enqueueSnackbar(response.message,{ variant: `error` });
      return false;
    }
    return true;
  }
  const login = async (formData) => {
    if(await validateInput(formData)){
      const response = await performApiCall();
      if(response){
       setData(data => ({
         ...data,
         username: "",
         password: "",
       }));
        enqueueSnackbar("Logged in ",{ variant: `success` });
        history.push("/");
      }
    }
     
  };

  // TODO: CRIO_TASK_MODULE_LOGIN - Validate the input
  /**
   * Validate the input values so that any bad or illegal values are not passed to the backend.
   *
   * @param {{ username: string, password: string }} data
   *  Object with values of username, password and confirm password user entered to register
   *
   * @returns {boolean}
   *    Whether validation has passed or not
   *
   * Return false and show warning message if any validation condition fails, otherwise return true.
   * (NOTE: The error messages to be shown for each of these cases, are given with them)
   * -    Check that username field is not an empty value - "Username is a required field"
   * -    Check that password field is not an empty value - "Password is a required field"
   */
  const validateInput = (data) => {
    if(!data.username || data.username.length==0) {
      enqueueSnackbar("Username is a required field",{ variant: `warning` });
      return false;
    }
    if(data.username && data.username.length <6) 
    {
      enqueueSnackbar("Username must be at least 6 characters",{ variant: `warning` });
      return false;
    }
    if(!data.password|| data.password.length==0) {
      enqueueSnackbar("Password is a required field",{ variant: `warning` });
      return false;
    }
    if(data.password && data.password.length < 6) {
      enqueueSnackbar("Password must be at least 6 characters",{ variant: `warning` });
      return false;
    }
    return true;
  };

  // TODO: CRIO_TASK_MODULE_LOGIN - Persist user's login information
  /**
   * Store the login information so that it can be used to identify the user in subsequent API calls
   *
   * @param {string} token
   *    API token used for authentication of requests after logging in
   * @param {string} username
   *    Username of the logged in user
   * @param {string} balance
   *    Wallet balance amount of the logged in user
   *
   * Make use of localStorage: https://developer.mozilla.org/en-US/docs/Web/API/Window/localStorage
   * -    `token` field in localStorage can be used to store the Oauth token
   * -    `username` field in localStorage can be used to store the username that the user is logged in as
   * -    `balance` field in localStorage can be used to store the balance amount in the user's wallet
   */
  const persistLogin = async (token, username, balance) => {
    localStorage.setItem("username",username);
      localStorage.setItem("token",token);
      localStorage.setItem("balance",balance);
  };

  return (
    <Box
      display="flex"
      flexDirection="column"
      justifyContent="space-between"
      minHeight="100vh"
    >
      <Header hasHiddenAuthButtons history={history}/>
      <Box className="content">
        <Stack spacing={2} className="form">
        <h2 className="title">Login</h2>
          <TextField
            id="username"
            label="Username"
            variant="outlined"
            title="Username"
            name="username"
            placeholder="Enter Username"
            onChange = {(e) => { handleInput(e)}}
            size="small"
            fullWidth
          />
          <TextField
            id="password"
            variant="outlined"
            label="Password"
            name="password"
            type="password"  
            helperText="Password must be atleast 6 characters length"
            size="small"
            onChange = {(e) => { handleInput(e)}}
            fullWidth
          />
          <Button  variant="contained" onClick={() => {login(data)}} >
            Login to QKart
          </Button>
          {loading && <CircularProgress />}
          <p className="secondary-action">
            Doesn't have an account?{" "}
            <Link to="/register"><a className="link">
              Register now
            </a></Link>
          </p>
        </Stack>
      </Box>
      <Footer />
    </Box>
  );
  };
export default Login;
