import { Search, SentimentDissatisfied } from "@mui/icons-material";
import {
  CircularProgress,
  Grid,
  InputAdornment,
  TextField,
} from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import { useSnackbar } from "notistack";
import React, { useEffect, useState } from "react";
import { config } from "../App";
import Footer from "./Footer";
import Header from "./Header";
import Cart, { generateCartItemsFrom } from "./Cart";
import "./Products.css";
import ProductCard from "./ProductCard";

// Definition of Data Structures used
/**
 * @typedef {Object} Product - Data on product available to buy
 * 
 * @property {string} name - The name or title of the product
 * @property {string} category - The category that the product belongs to
 * @property {number} cost - The price to buy the product
 * @property {number} rating - The aggregate rating of the product (integer out of five)
 * @property {string} image - Contains URL for the product image
 * @property {string} _id - Unique ID for the product
 */

const Products = () => {
  const { enqueueSnackbar } = useSnackbar();
  const [loading,setLoading] = useState(false);
  const [products,setProducts] = useState([]);
  const [filteredProducts,setFilteredProducts] = useState([]);
  const [items,setItems] =useState([]);
  const token= localStorage.getItem("token");
  
  // TODO: CRIO_TASK_MODULE_PRODUCTS - Fetch products data and store it
  /**
   * Make API call to get the products list and store it to display the products
   *
   * @returns { Array.<Product> }
   *      Array of objects with complete data on all available products
   *
   * API endpoint - "GET /products"
   *
   * Example for successful response from backend:
   * HTTP 200
   * [
   *      {
   *          "name": "iPhone XR",
   *          "category": "Phones",
   *          "cost": 100,
   *          "rating": 4,
   *          "image": "https://i.imgur.com/lulqWzW.jpg",
   *          "_id": "v4sLtEcMpzabRyfx"
   *      },
   *      {
   *          "name": "Basketball",
   *          "category": "Sports",
   *          "cost": 100,
   *          "rating": 5,
   *          "image": "https://i.imgur.com/lulqWzW.jpg",
   *          "_id": "upLK9JbQ4rMhTwt4"
   *      }
   * ]
   *
   * Example for failed response from backend:
   * HTTP 500
   * {
   *      "success": false,
   *      "message": "Something went wrong. Check the backend console for more details"
   * }
   */
const fetchCart = async (token) => {
  if(!token) return;
  try{
    const response = await axios.get(`${config.endpoint}/cart`,{
      headers: {
        Authorization : `Bearer ${token}`,
      }
    });
    return response.data;
  }catch {
    enqueueSnackbar("Could not fetch cart details. Check that the backend is running, reachable and returns valid JSON",{variant: "error"});
    return null;
  } 
}

useEffect(() => {
    getProducts();  
},[]);

useEffect(() => {
  fetchCart(token).then((cartData) => generateCartItemsFrom(cartData,products)).then((cartItems) => setItems(cartItems));  
},[products]);

  const validateResponse = (errored, response) => {
    if (errored || (!response.length && !response.message)) {
      enqueueSnackbar(
        "Could not fetch products. Check that the backend is running, reachable and returns valid JSON."
      ,{variant : "error"});
      return false;
    }

    if (!response.length) {
      enqueueSnackbar("No products found ",{variant : "error"});
      return false;
    }

    return true;
  };
  const validateSearchResponse = (errored, response) => {
     if (errored || (!response.length && !response.message) ||!response.length) {
      enqueueSnackbar("No products found ",{variant : "error"});
      setFilteredProducts([]);
      return false;
    }

    return true;
  };

  const performAPICall = async () => {
    let response = {};
    let errored = false;

    setLoading(true);

    try {
      response = (await axios.get(`${config.endpoint}/products`)).data;
    } catch (e) {
      errored = true;
    }
    setLoading(false);

    if (validateResponse(errored, response)) {
      return response;
    }
  };

  const getProducts = async () => {
    let response = await performAPICall();
    if(response){
      setProducts(response);
      setFilteredProducts(response);
    }  
  };
  const isItemInCart = (items,productId) => {
    return items.findIndex((item) => item.productId === productId) !== -1;
  }

  const addToCart = async(token,items,productId,products,qty,options={preventDuplicate: false})=>{
    if(!token){
      enqueueSnackbar("Please log in to add item to cart",{variant: "warning"});
      return;
    }
    if(options.preventDuplicate && isItemInCart(items,productId)) {
      enqueueSnackbar("Item already in cart. Use the cart sidebar to update quantity or remove item.",{variant : "warning,"});
      return;
    }

    try{
      const response = await axios.post(
        `${config.endpoint}/cart`,
        {productId, qty},
        {
          headers: {
            Authorization: `Bearer ${token}`,
          }
        }
      );
      const cartItems = generateCartItemsFrom(response.data,products);
      setItems(cartItems);
    }catch(e) {
      if(e.response){
        enqueueSnackbar(e.response.data.message, { variant: "error"});
      }else {
        enqueueSnackbar("Could not fetch products. Check that the backend is running, reachabe and returns valid JSON.",{
          variant: "error",
        });
      }
    }
  }

  // TODO: CRIO_TASK_MODULE_PRODUCTS - Implement search logic
  /**
   * Definition for search handler
   * This is the function that is called on adding new search keys
   *
   * @param {string} text
   *    Text user types in the search bar. To filter the displayed products based on this text.
   *
   * @returns { Array.<Product> }
   *      Array of objects with complete data on filtered set of products
   *
   * API endpoint - "GET /products/search?value=<search-query>"
   *
   */
  const performSearch = async (value) => {
      let response = {};
       let errored = false;
   
       setLoading(true);
   
       try {
         response = (await axios.get(`${config.endpoint}/products/search?value=${value}`)).data;
       } catch (e) {
         errored = true;
       }
       setLoading(false);
   
       if (validateSearchResponse(errored, response)) {
        setFilteredProducts(response);
       }
  };

  // TODO: CRIO_TASK_MODULE_PRODUCTS - Optimise API calls with debounce search implementation
  /**
   * Definition for debounce handler
   * With debounce, this is the function to be called whenever the user types text in the searchbar field
   *
   * @param {{ target: { value: string } }} event
   *    JS event object emitted from the search input field
   *
   * @param {NodeJS.Timeout} debounceTimeout
   *    Timer id set for the previous debounce call
   *
   */
  let debounceTimeout=0;
  const debounceSearch = (event,delay) => {
    const value = event.target.value;

    if (debounceTimeout) {
      clearTimeout(debounceTimeout);
    }

     debounceTimeout = setTimeout(() => {
      performSearch(value);
    }, delay);
  }
  return (
    <div>
      <Header>
        {/* TODO: CRIO_TASK_MODULE_PRODUCTS - Display search bar in the header for Products page */}
        <TextField
        className="search-desktop"
        size="small"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Search color="primary" />
            </InputAdornment>
          ),
        }}
        onChange={(event) => {debounceSearch(event,300)}}
        placeholder="Search for items/categories"
        name="search"
      />
      </Header>

      {/* Search view for mobiles */}
      <TextField
        className="search-mobile"
        size="small"
        fullWidth
        onChange={(event) => {debounceSearch(event,300)}}
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Search color="primary" />
            </InputAdornment>
          ),
        }}
        placeholder="Search for items/categories"
        name="search"
      />
       <Grid container>
         <Grid item className="product-grid" xs={12} >
           <Box className="hero">
             <p className="hero-heading">
               Indias <span className="hero-highlight">FASTEST DELIVERY</span>{" "}
               to your door step
             </p>
           </Box>
         </Grid>
        {products.length!==0 ? (<Grid container spacing={2} md={token ? 8 : 12} xs={12} style={{margin : "20px"}}>
         
         {filteredProducts.map((product) => (
           
           <ProductCard key={product.id} product={product} handleAddToCart={async() => await addToCart(token,items,product._id,products,1,{preventDuplicate: true})} />
         ))}
       </Grid>):  loading ? <div className="loading"><CircularProgress />Loading Products...</div> : (<div>
         No Products Found
        </div>)}
        {token ? (<Grid xs={12} md={3} bg="#E9F5E1" style={{margin : "20px"}}><Cart items={items} products={products} handleQuantity={addToCart} /></Grid>): null}
         
       </Grid>
       
      <Footer />
    </div>
  );
};

export default Products;
